
async function DownloadPm(time, interval) {
    const URL = `https://gis.krasn.ru/sc/api/1.0/projects/9/aggvalues?&sites=3837&key=654hblgm9gl8367h&time_begin=${time[0]} 00:00:00&time_end=${time[1]} 00:00:00&time_interval=${interval}&indicators=348&limit=30000'`;
    const responce = fetch(URL);
    const result = responce.then(e => XML2JSON(e));
    return result;
}
async function DownloadWind(time, interval) {
    const URL = `https://gis.krasn.ru/sc/api/1.0/projects/1/aggvalues?&sites=3833&key=654hblgm9gl8367h&time_begin=${time[0]} 00:00:00&time_end=${time[1]} 00:00:00&time_interval=${interval}&indicators=101&limit=30000'`;
    const responce = fetch(URL);
    const result = responce.then(e => XML2JSON(e));
    return result;
}

function DirectionToText(value) {
    if (value >= 0 && value < 11.25)
        return "С"
    if (value >= 11.25 && value < 33.75)
        return "ССВ"
    if (value >= 33.75 && value < 56.25)
        return "СВ"
    if (value >= 56.25 && value < 78.75)
        return "ВСВ"
    if (value >= 78.75 && value < 101.25)
        return "В"
    if (value >= 101.25 && value < 123.75)
        return "ВЮВ"
    if (value >= 123.75 && value < 146.25)
        return "ЮВ"
    if (value >= 146.25 && value < 168.75)
        return "ЮЮВ"
    if (value >= 168.75 && value < 191.25)
        return "Ю"
    if (value >= 191.25 && value < 213.75)
        return "ЮЮЗ"
    if (value >= 213.75 && value < 236.25)
        return "ЮЗ"
    if (value >= 236.25 && value < 258.75)
        return "ЗЮЗ"
    if (value >= 258.75 && value < 281.25)
        return "З"
    if (value >= 281.25 && value < 303.75)
        return "ЗСЗ"
    if (value >= 303.75 && value < 326.25)
        return "СЗ"
    if (value >= 326.25 && value < 348.75)
        return "ССЗ"
    if (value >= 348.75)
        return "С"
}

async function XML2JSON(e) {
    const r = e.text().then(e => {
        const result = [];
        const times = [];
        const dom = new DOMParser();
        const xml = dom.parseFromString(e, "text/xml");
        const aggvalues = xml.getElementsByTagName("aggvalue");
        aggvalues.forEach((element) => {
            const indicator = element.getAttribute("indicator")
            const time = element.getAttribute("time").substring(0, element.getAttribute("time").length - 3);
            const value = Number(element.getElementsByTagName("avg")[0].innerHTML).toFixed(4);
            if (indicator == 101) {
                result.push(DirectionToText(value))
            }
            else {
                result.push(value * 1000);
            }
            times.push(time);
        })
        return { result, times };

    })
    return r;
}

export { DownloadPm, DownloadWind }