<template>
  <div id="app">
    {{ text }}
    <Settings @ref="Refresh" />
    <Topo :data="data3D" v-if="load" />
  </div>
</template>

<script>
import Topo from "./components/Topographical3D";
import Settings from "./components/Settings";
import { DownloadPm } from "./scripts/Download";
import { DownloadWind } from "./scripts/Download";
export default {
  name: "App",
  data() {
    return {
      data3D: "",
      data2D: "",
      load: false,
      text: "",
    };
  },
  components: {
    Settings,
    Topo,
  },
  methods: {
    Refresh(time, interval) {
      this.text = "ЗАГРУЗКА...";
      DownloadPm(time, interval).then((pm) => {
        DownloadWind(time, interval).then((wind) => {
          this.data3D = [
            {
              opacity: 1,
              color: "rgb(100,100,200)",
              type: "mesh3d",
              x: wind.result,
              y: pm.times,
              z: pm.result,
            },
          ];
          /*   this.data2D = [
            {
              opacity: 0.9,
              color: "rgb(100,100,200)",
              type: "histogram2dcontour",
              x: wind.result,
              y: pm.result,
            },
          ]; */
          this.text = "ГОТОВО";
          this.load = true;
        });
      });
    },
  },

  mounted() {
    this.Refresh(["2019-06-01", "2019-08-31"], "6hour");
  },
};
</script>

