<template>
  <Plotly
    v-bind:data="data"
    :layout="layout"
    :config="config"
    :display-mode-bar="false"
  ></Plotly>
</template>

<script>
import { Plotly } from "vue-plotly";
export default {
  components: {
    Plotly,
  },
  props: ["data"],
  data() {
    return {
      config: { responsive: true },
      layout: {
        width: 1000,
        height: 700,
        margin: {
          l: 50,
          r: 50,
          b: 100,
          t: 0,
          pad: 4,
        },
      },
    };
  },
};
</script>

