<template>
  <div class="datapicker">
    <p>
      <span class="block">Дата</span>
      <date-picker
        class="block"
        v-model="time"
        range
        valueType="format"
      ></date-picker>
    </p>
    <p>
      <span class="block">Агрегирование</span>
      <select
        v-model="interval"
        class="block"
        style="height: 34px; font-size: 14px"
      >
        <option value="1hour">Час</option>
        <option value="6hour">6 часов</option>
        <option value="12hour">12 часов</option>
        <option value="1day">День</option>
      </select>
    </p>
    <button @click="Refresh" style="height: 34px">Обновить</button>
  </div>
</template>

<script>
import DatePicker from "vue2-datepicker";
import "vue2-datepicker/index.css";
export default {
  components: { DatePicker },
  data() {
    return { time: ["2019-06-01", "2019-08-31"], interval: "6hour" };
  },
  methods: {
    Refresh() {
      this.$emit("ref", this.time, this.interval);
    },
  },
};
</script>

<style>
.block {
  margin: 1px;
}
</style>